import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/views/Home";
import Contents from "@/views/Contents";
import File from "@/views/File";
import Stars from "@/views/Stars";
import Login from "@/views/Login";
import Love from "@/views/Love";
import Image from "@/views/Image";
import Kernel from "@/views/Kernel";

Vue.use(VueRouter);

const routes = [
  {
    path: "/OYO",
    redirect: "/OYO/home"
  },
  {
    path: "/OYO/home",
    name: "Home",
    component: Home,
    meta: {
      title: ""
    }
  },
  {
    path: "/OYO/contents*",
    name: "Files",
    component: Contents,
    meta: {
      title: ""
    }
  },
  {
    path: "/OYO/file*",
    name: "File",
    component: File,
    meta: {
      title: ""
    }
  },
  {
    path: "/OYO/image*",
    name: "Image",
    component: Image,
    meta: {
      title: ""
    }
  },
  {
    path: "/OYO/kernel*",
    name: "Kernel",
    component: Kernel,
    meta: {
      title: ""
    }
  },
  {
    path: "/OYO/stars",
    name: "Stars",
    component: Stars,
    meta: {
      title: ""
    }
  },
  {
    path: "/OYO/login",
    name: "Login",
    component: Login,
    meta: {
      title: ""
    }
  },
  {
    path: "/OYO/love",
    name: "Love",
    component: Love,
    meta: {
      title: " "
    }
  }
];

const router = new VueRouter({
  mode: "history",
  linkActiveClass: "active",
  routes: routes
});

router.beforeEach((to, from, next) => {
  to.matched[0]
    ? (document.title = "OYO - " + to.matched[0].meta.title)
    : (document.title = "OYO");
  next();
});

export default router;
